<template>
  <div id="app">
    <main>
      {{ Timer }}
    </main>
    <button @click='start(1)' v-if='isActive'>スタート</button>
    <button @click='start(2)' v-if='isActive'>30分からスタート</button>
    <button @click='start(3)' v-if='isActive'>1時間からスタート</button>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      startTime: 0,
      elapsedTime: 0,
      h: 0,
      m: 0,
      s: 0,
      ms: 0,
      Timer: '00:00:00',
      isActive: true
    }
  },
  methods: {
    start (flag) {
      var dt
      this.isActive = !this.isActive
      switch (flag) {
        case 1:
          this.startTime = new Date()
          break
        case 2:
          dt = new Date()
          this.startTime = dt.setMinutes(dt.getMinutes() + 30)
          break
        case 3:
          dt = new Date()
          this.startTime = dt.setMinutes(dt.getHours() + 1)
          break
      }
      this.countUp()
    },
    countUp () {
      setTimeout(() => {
        this.elapsedTime = new Date() - this.startTime
        this.updateTimetText()
        this.countUp()
      }, 10)
    },
    updateTimetText () {
      this.h = Math.floor(this.elapsedTime / 360000)
      this.m = Math.floor(this.elapsedTime / 60000)
      this.s = Math.floor(this.elapsedTime % 60000 / 1000)
      // this.ms = this.elapsedTime % 1000

      this.Timer = ('0' + this.h).slice(-2) + ':' + ('0' + this.m).slice(-2) + ':' + ('0' + this.s).slice(-2)
    }
  }
}
</script>

<style>
body {
  margin: 0;
  background-color: black;
}

button {
  color: red;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: 32px;
  color: white;
}

main {
  margin: 15px 0 0 15px;
  font-size: 3rem;
}

header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
}

header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}
</style>



